import React from "react";
import uuid from "uuid";

function Pagination({
  currentPageNumber,
  totalElementsNumber,
  setCurrentPageNumber,
  numberOfPostsPerPage,
}) {
  const pages = [];
  for (
    let i = 1;
    i <= Math.ceil(totalElementsNumber / numberOfPostsPerPage);
    i++
  ) {
    pages.push(i);
  }
  return (
    <nav aria-label="Applicants pagination">
      <ul className="pagination pagination-lg">
        <li
          className={`page-item ${currentPageNumber <= 1 && "disabled"}`}
          onClick={e => {
            e.preventDefault();
            if (currentPageNumber > 1) {
              setCurrentPageNumber(currentPageNumber - 1);
            }
          }}
        >
          <a className="page-link" href="/#" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        {pages.map(page => (
          <li key={uuid()} className={`page-item`}>
            <a
              className="page-link"
              href="/#"
              onClick={e => {
                e.preventDefault();
                setCurrentPageNumber(page);
              }}
              style={{ opacity: page === currentPageNumber ? 1 : 0.5 }}
            >
              {page}
            </a>
          </li>
        ))}

        <li
          className={`page-item ${
            currentPageNumber >=
              Math.ceil(totalElementsNumber / numberOfPostsPerPage) &&
            "disabled"
          }`}
          onClick={e => {
            e.preventDefault();
            if (
              currentPageNumber <
              Math.ceil(totalElementsNumber / numberOfPostsPerPage)
            ) {
              setCurrentPageNumber(currentPageNumber + 1);
            }
          }}
        >
          <a className="page-link" href="/#" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  );
}

export default Pagination;
