import React from "react";
import { profile } from "../../../utils/fixtures";

function FilterationForm({
  onFormSubmitHandler = () => {},
  resetFilters = () => {},
  onChangeHandler = () => {},
  filters = {},
}) {
  return (
    <form
      className="form-row align-items-center "
      onSubmit={onFormSubmitHandler}
    >
      <div className="col-12 col-md-6 pr-0 pr-md-5">
        <div className="d-flex">
          <div className="form-group w-50 pr-2">
            <label htmlFor="searchBy">Search by</label>
            <select
              name="searchBy"
              value={filters.searchBy}
              onChange={onChangeHandler}
              className="custom-select form-control"
              id="searchBy"
            >
              <option value="name">Applicant name</option>
              <option value="jobTitle">Job title</option>
            </select>
          </div>
          <div className="form-group pl-2 w-50">
            <label htmlFor="language">Language</label>
            <select
              name="language"
              id="language"
              value={filters.language}
              onChange={onChangeHandler}
              className="custom-select form-control"
            >
              {profile.languages.map(lang => (
                <>
                  <option value="">Has Language</option>
                  <option key={lang} value={lang}>
                    {lang}
                  </option>
                </>
              ))}
            </select>
          </div>
        </div>
        <div className="d-flex">
          <div className="input-group pr-2 w-50">
            <label htmlFor="minAge">
              Minimum age
              <span className="badge badge-pill badge-primary ml-3">
                {filters.minAge || "Not selected"}
              </span>
            </label>
            <input
              value={filters.minAge}
              name="minAge"
              onChange={onChangeHandler}
              type="range"
              className="custom-range"
              min="18"
              max="80"
              id="minAge"
            />
          </div>

          <div className="input-group pl-2 w-50">
            <label htmlFor="maxAge">
              Maximum age
              <span className="badge badge-pill badge-primary ml-3">
                {filters.maxAge || "Not selected"}
              </span>
            </label>

            <input
              value={filters.maxAge}
              name="maxAge"
              onChange={onChangeHandler}
              type="range"
              className="custom-range"
              id="maxAge"
              min="18"
              max="80"
            />
          </div>
        </div>
        <button
          onClick={resetFilters}
          type="button"
          className="btn btn-secondary col my-5"
        >
          Reset filters
        </button>
      </div>

      <div className="col-12 col-md-6 pl-0 pl-md-5">
        <div className="input-group">
          <input
            name="searchText"
            placeholder="Search for profiles"
            value={filters.searchText}
            onChange={onChangeHandler}
            className="form-control"
            style={{ height: 35 }}
          />
          <div className="input-group-append">
            <button className="btn btn-primary" type="submit">
              Search
            </button>
          </div>
          <p className="mt-3 text-secondary">
            Search is set to applicant name by default, if you want other
            criteria please choose other filters.
          </p>
        </div>
      </div>
    </form>
  );
}

export default FilterationForm;
