import React, { Component } from 'react'
import io from "socket.io-client"
import SimplePeer from "simple-peer"
import {socket} from "../../../index"
import $ from "jquery"
import { connect } from 'react-redux'
import queryString from 'query-string'
import {withRouter} from "react-router-dom"
class Interview extends Component {
    constructor(props){
        super(props)
        this.localVideoref = React.createRef()
        this.remoteVideoref = React.createRef()
    
 
        this.candidates = []
    }
        state={
            client:{},
            companyId:this.props.match.params.company,
           applicantId:this.props.match.params.applicant,
            streamMode:false,
            mode:"Connecting",
           loader:true,
           user:this.props.user.userData,
           stream:"",
           anotherUserId:null,
           ended:false,
           


        }
    componentDidUpdate(prevProps, prevState) {
        
    
        }
        
     
    componentDidMount(){

    //console.log("MOUNT ",this.props)
    /*
  if(localStorage.getItem("#$import")!="#$im"){
window.top.close();

    window.location="/jobs"
   
    

    this.setState({ring:false})


}else{
    localStorage.removeItem("#$import")

}*/
//USING WEBS
      // socket.on("CreatePeer",this.MakePeer)

     socket.on('OfferOrAnswer', ({sdp,userId,companyId}) => {
       console.log("OFFER OR ANWS ",companyId)
       const values = queryString.parse(this.props.location.search)
   

        //this.textref.value = JSON.stringify(sdp)
  
        this.pc.setRemoteDescription(new RTCSessionDescription(sdp)).then(()=>{
          if(values.type){
            this.setState({anotherUserId:companyId})
              this.createAnswer()
   
            
           }else{
             this.setState({loader:false})
           }
        })

     
      })

    socket.on('candidate', (candidate) => {
   
        this.pc.addIceCandidate(new RTCIceCandidate(candidate))
      })
  
    // const pc_config = null

    const pc_config = {
        "iceServers": [
          // {
          //   urls: 'stun:[STUN_IP]:[PORT]',
          //   'credentials': '[YOR CREDENTIALS]',
          //   'username': '[USERNAME]'
          // },
          {
            urls : 'stun:stun.l.google.com:19302'
          }
        ]
      }
  
      // https://developer.mozilla.org/en-US/docs/Web/API/RTCPeerConnection
      // create an instance of RTCPeerConnection
      this.pc = new RTCPeerConnection(pc_config)
  
      // triggered when a new candidate is returned
      this.pc.onicecandidate = (e) => {
        // send the candidates to the remote peer
        // see addCandidate below to be triggered on the remote peer
        if (e.candidate) {
          // console.log(JSON.stringify(e.candidate))
        
          let userId
          let companyId=null
          if(values.type){
           userId=this.state.anotherUserId
          }else{
             userId=this.state.applicantId
             
          }
          console.log("USER ID CANDIDATE ",userId)
          this.sendToPeer('candidate', {candidate:e.candidate,userId})
        }
      }
      this.pc.onconnectionstatechange = (event)=>{
      switch(this.pc.connectionState) {
       
          case "disconnected":
           this.setState({ended:true})

          case "failed":
            this.setState({ended:true})

            break;
          case "closed":
            console.log("closed")
            break;
        }
      }
  
      // triggered when there is a change in connection state
      this.pc.oniceconnectionstatechange = (e) => {
        console.log(e)
      }
  
      // triggered when a stream is added to pc, see below - this.pc.addStream(stream)
      // this.pc.onaddstream = (e) => {
      //   this.remoteVideoref.current.srcObject = e.stream
      // }
  
      this.pc.ontrack = (e) => {
       // debugger
       this.setState({loader:false})
        this.remoteVideoref.current.srcObject = e.streams[0]
      }
  
      // called when getUserMedia() successfully returns - see below
      // getUserMedia() returns a MediaStream object (https://developer.mozilla.org/en-US/docs/Web/API/MediaStream)
      const success = (stream) => {
        window.localStream = stream
        this.localVideoref.current.srcObject = stream
        this.pc.addStream(stream)
      }
  
      // called when getUserMedia() fails - see below
      const failure = (e) => {
        console.log('getUserMedia Error: ', e)
      }
      const constraints = {
        // audio: true,
        video: true,
        //audio:true,
        // video: {
        //   width: 1280,
        //   height: 720
        // },
        // video: {
        //   width: { min: 1280 },
        // }
        options: {
          mirror: true,
        }
      }
  
      // https://developer.mozilla.org/en-US/docs/Web/API/MediaDevices/getUserMedia
      navigator.mediaDevices.getUserMedia(constraints)
        .then(success)
        .catch(failure)


       
       /*
        socket.on("BackOffer",this.FrontAnswer)
        
        socket.on("BackAnswer",this.SignalAnswer)
        socket.on("SessionActive",this.SessionActive)
*/
        let user=this.props.user.userData
        let profile=this.props.profile
        socket.on("rejected",({companyId})=>{
           // $("#ring").pause()
           localStorage.removeItem("#$import","#$im")

            this.setState({mode:"Busy"})
          


         })
        
         socket.on("accepted",({companyId})=>{
            // $("#ring").pause()
            localStorage.removeItem("#$import","#$im")
          console.log("ACCEPETEEEEEEED")
          //  socket.emit("CreatePeer",{userId:user._id,applicantId:this.state.applicantId})
          this.createOffer()

             this.setState({mode:"Starting video call"})
           
 
 
          })
              const values = queryString.parse(this.props.location.search)
           if(values.type){
            
                          this.setState({mode:"Starting video call"})
                          socket.emit("accept",{companyId:this.state.companyId,myId:user._id})

              }


              if(!values.type)
              {
            //    this.createOffer();
                socket.on("UserConnected",(applicantId)=>{
                  console.log("USSSSSSSSSSSSSSSSSSSSSSSER connected")
               //   this.createOffer(applicantId);
                 socket.emit("CallUser",({applicantId,companyName:user.name,companyId:user._id,companyImg:profile.image}))
                  this.setState({mode:"Ring"})
              })
      
              }

        const video=document.getElementById("my-video")
        let companyId=this.state.companyId;
        let applicantId=this.state.applicantId;
        socket.emit("CheckOnline",{applicantId,companyId})

      /*  navigator.mediaDevices.getUserMedia({video:true,audio:true}) // get browser perm
        .then(stream=>{
            this.setState({stream})
            this.startStreaming(stream)

            console.log(stream)
            const values = queryString.parse(this.props.location.search)
            if(values.type){
                socket.emit("accept",{companyId:this.state.companyId})

              console.log("MOUNTTTTTTTTTTTTTTTT ",values)
            }
         
        }).catch((e)=>{
            console.log("EERRRRORR ",e)

        })*/
       
       
    }
    
    cancel=()=>{
        window.top.close()
        socket.emit("cancel",{applicantId:this.state.applicantId})
    }
    
  sendToPeer = (messageType, payload) => {
  socket.emit(messageType, {
      socketID: socket.id,
      payload
    })
  }

  /* ACTION METHODS FROM THE BUTTONS ON SCREEN */

  createOffer = () => {
    console.log('Offer '+this.state.user._id)

    // https://developer.mozilla.org/en-US/docs/Web/API/RTCPeerConnection/createOffer
    // initiates the creation of SDP
    this.pc.createOffer({ offerToReceiveVideo: 1 })
      .then(sdp => {
        // console.log(JSON.stringify(sdp))

        // set offer sdp as local description
        this.pc.setLocalDescription(sdp)
 
        this.sendToPeer('OfferOrAnswer', {sdp,userId:this.state.applicantId,companyId:this.state.user._id})
    })
  }

  // https://developer.mozilla.org/en-US/docs/Web/API/RTCPeerConnection/createAnswer
  // creates an SDP answer to an offer received from remote peer
  createAnswer = () => {
    console.log('Answer')
    this.pc.createAnswer({ offerToReceiveVideo: 1 })
      .then(sdp => {
        // console.log(JSON.stringify(sdp))

        // set answer sdp as local description
        this.pc.setLocalDescription(sdp)
        const values = queryString.parse(this.props.location.search)
        let userId=this.state.applicantId
        if(values.type){
         userId=this.state.anotherUserId
        }
        this.sendToPeer('OfferOrAnswer', {sdp,userId})
    })
  }

  setRemoteDescription = () => {
    // retrieve and parse the SDP copied from the remote peer
    const desc = JSON.parse(this.textref.value)

    // set sdp as remote description
    this.pc.setRemoteDescription(new RTCSessionDescription(desc))
  }

  addCandidate = () => {
    // retrieve and parse the Candidate copied from the remote peer
    // const candidate = JSON.parse(this.textref.value)
    // console.log('Adding candidate:', candidate)

    // add the candidate to the peer connection
    // this.pc.addIceCandidate(new RTCIceCandidate(candidate))

    this.candidates.forEach(candidate => {
      console.log(JSON.stringify(candidate))
      this.pc.addIceCandidate(new RTCIceCandidate(candidate))
    });
  }
    
    render() {
        
        return (
            <section className="interview">
       
        {
        
        /*
              <div>
     
        <br />

        <button onClick={this.createOffer}>Offer</button>
        <button onClick={this.createAnswer}>Answer</button>

        <br />
        <textarea style={{ width: 450, height:40 }} ref={ref => { this.textref = ref }} />

        
        <br />
        <button onClick={this.setRemoteDescription}>Set Remote Desc</button>
        <button onClick={this.addCandidate}>Add Candidate</button> 
          </div>
        
        */}

                     {this.state.mode=="Ring"&&<audio id="ring" autoPlay loop>

<source src="/audio/sms-alert-3-daniel_simon.mp3" type="audio/mpeg" />
</audio>}
      
 
                <div className="container">
                   {this.state.loader&& <div style={{zIndex:9999}} className="interview-overlay">
                   {this.state.mode=="Busy"?  <React.Fragment>
                 
                        <p>Busy</p>
                   </React.Fragment>: <React.Fragment>
                   <img className="" src="/images/Eclipse-loader.gif"></img>
                        <p>{this.state.mode}...</p>
                        {this.state.user.type&&<button onClick={this.cancel} className="btn btn-danger">cancel</button>}
                   </React.Fragment>}
                      
        
                    </div>}
                    {this.state.ended?<div style={{zIndex:9999}} className="interview-overlay">ENDD</div>:
                     <div className="row">
                     <div className="col-md-3 col-sm-12">
                     <div className="interview__my-video bounded-video">
                      <video className="video" id="my-video" ref={ this.localVideoref } autoPlay></video>
                     </div>
                     </div>
                     <div className="col-md-9 col-sm-12">
                    
                     <div id="interview__another-person-video" className="interview__another-person-video bounded-video">
            
     <video
       className="video"
       ref={ this.remoteVideoref }
       autoPlay>
     </video>
                     </div>
                     </div>
                 </div>
                    }

                   
                </div>
                
            </section>
        )
        
    }
}

const mapStateToProps = (state, props) => ({
    isAuth: state.auth.isAuth,
    auth: state.auth,
    user: state.user,
    profile:state.profile.profileData

  });
  
  export default connect(mapStateToProps,undefined)(withRouter(Interview));
  
