import React, { useState } from "react";
import AnalysisDataCard from "./AnalysisDataCard";
import LineChart from "../../reusable/charts/LineChart";
function WebsiteAnalysis() {
  const [websiteMode, setWebsiteMode] = useState("on");
  const toggleWebsiteMode = () => {
    const mode = websiteMode === "on" ? "off" : "on";
    setWebsiteMode(mode);
  };
  return (
    <div className="avoid-navbar website-analysis pt-5">
      <div className="row">
        <AnalysisDataCard info="111,545" title="views" />
        <AnalysisDataCard info="111,545" title="users" />
        <div className="main-card-layout website-mode-card">
          <span>website mode</span>
          <div className="toggle-website-mode" onClick={toggleWebsiteMode}>
            <span className={`circle ${websiteMode}`} />
          </div>
        </div>
      </div>
      <div className="row mt-5">
        <LineChart />
      </div>
    </div>
  );
}

export default WebsiteAnalysis;
