import React from "react";
import AdminAnalysisSidebar from "../../../routes/AdminAnalysisSidebar";
import { NavLink } from "react-router-dom";

function AdminAnalysis() {
  return (
    <div className="admin-analysis">
      <nav className="sidebar analysis-sidebar">
        <ul className="list-unstyled">
          <NavLink activeClassName="active" to="/analysis/website">
            <li>
              <i className="fas fa-globe"></i>
              Website
            </li>
          </NavLink>
          <NavLink activeClassName="active" to="/analysis/general">
            <li>
              <i className="fas fa-chart-line"></i>
              Analysis
            </li>
          </NavLink>
          <NavLink activeClassName="active" to="/analysis/events">
            <li>
              <i className="fas fa-calendar-alt"></i>
              Events
            </li>
          </NavLink>
          <NavLink activeClassName="active" to="/analysis/payment">
            <li>
              <i className="fas fa-money-check-alt"></i>
              Payment
            </li>
          </NavLink>
          <NavLink activeClassName="active" to="/analysis/settings">
            <li>
              <i className="fas fa-cog"></i>
              settings
            </li>
          </NavLink>
        </ul>
      </nav>

      <main className="content  avoid-sidebar--left">
        <div className="container avoid-sidebar--left pt-5">
          <AdminAnalysisSidebar />
        </div>
      </main>
    </div>
  );
}

export default AdminAnalysis;
