import React, { useState, useCallback, useEffect } from "react";
import FilterationForm from "./FilterationForm";
import Pagination from "./Pagination";
import Axios from "axios";
function SearchProfiles() {
  const [filters, setFilters] = useState({
    searchText: "",
    searchBy: "name", //name || job title
    minAge: "",
    maxAge: "",
    language: "",
  });

  const [loading, setLoading] = useState(null);
  const [posts, setPosts] = useState([]);
  const [numberOfPostsPerPage] = useState(10);
  const [currentPageNumber, setCurrentPageNumber] = useState(1);

  const onChangeHandler = useCallback(e => {
    const { name, value } = e.target;
    setFilters(prevFilters => ({ ...prevFilters, [name]: value }));
  }, []);
  const onFormSubmitHandler = useCallback(e => {
    e.preventDefault();
  }, []);

  const resetFilters = useCallback(() => {
    setFilters(prevFilters => ({
      ...prevFilters,
      searchBy: "name", //name || job title
      minAge: "",
      maxAge: "",
      language: "",
    }));
  }, []);
  useEffect(() => {
    (async () => {
      try {
        setLoading(true);
        const rowData = await fetch(
          "https://jsonplaceholder.typicode.com/todos"
        );
        const res = await rowData.json();
        setPosts(res.data);
        setLoading(false);
        console.log(res.data);
      } catch (error) {
        setLoading(undefined);
      }
    })();
  }, []);

  const lastPostNumber = currentPageNumber * numberOfPostsPerPage;
  const firstPostNumber = lastPostNumber - numberOfPostsPerPage;
  const currentPosts = posts?.slice(firstPostNumber, lastPostNumber);
  return (
    <div className="avoid-navbar pt-3">
      <div className="container" style={{ height: "90vh" }}>
        <div className="d-flex flex-column h-100">
          <FilterationForm
            onChangeHandler={onChangeHandler}
            resetFilters={resetFilters}
            onFormSubmitHandler={onFormSubmitHandler}
            filters={filters}
          />

          <main className="d-flex flex-1 flex-column flex-grow-1 bg-danger h-100">
            <section className="search-results flex-grow-1">hello</section>

            <div className="d-flex justify-content-center align-items-center flex-column">
              {currentPosts.map(post => (
                <p>{post.title}</p>
              ))}
              <Pagination
                currentPageNumber={currentPageNumber}
                totalElementsNumber={posts.length}
                setCurrentPageNumber={number => setCurrentPageNumber(number)}
                numberOfPostsPerPage={numberOfPostsPerPage}
              />
            </div>
          </main>
        </div>
      </div>
    </div>
  );
}

export default SearchProfiles;
