import React, { Component } from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { connect } from "react-redux";
import { MainPagesRoutes } from "./routes";
import { logout, fetchUserData, fetchProfile } from "./redux/actions";
import { NavbarGuest, NavbarUser } from "./components/reusable";
import Loader1 from "./components/reusable/loaders/Loader_1";
import { socket } from "./index";
import $ from "jquery";
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      appIsLoading: props.profile === null ? true : false,
      companyId: "",
      companyName: "",
      companyImg: "",
      userData: {},
      ring: false,
    };
  }
  OnCancel = () => {
    this.setState({ ring: false });
    $("#incoming-call").modal("hide");
  };
  componentDidMount() {
    // only fetch if user is not in redux store
    document.querySelector("*").scrollTop = 0;
    socket.on("cancel", this.OnCancel);
    
    socket.on("HideCalling",()=>{
      console.log("HIDE CALLING ")
      $("#incoming-call").modal("hide");
    })

    socket.on("ReceiveCall", ({ companyId, companyName, companyImg }) => {
      console.log("RECEIVE CALL");
      this.setState({ companyId, companyName, companyImg, ring: true });

      $("#incoming-call").modal({
        backdrop: "static",
        keyboard: false,
      });
    });
    if (this.props.isAuth) {
      this.props.fetchUserData();
    }
  }
  reject = () => {
    console.log("REJECT");

    this.setState({ ring: false });
    socket.emit("reject", { companyId: this.state.companyId ,myId:this.state.userData._id});
  };
  accept = () => {
    localStorage.setItem("#$import", "#$im");

    this.setState({ ring: false });

    this.popupCenter({
      url: `${window.location.origin}/interview/${this.state.companyId}/${this.state.userData._id}?type=accepted`,
      title: "workegypt",
      w: "900",
      h: "900",
    });
  };
  popupCenter = ({ url, title, w, h }) => {
    // Fixes dual-screen position                             Most browsers      Firefox
    const dualScreenLeft =
      window.screenLeft !== undefined ? window.screenLeft : window.screenX;
    const dualScreenTop =
      window.screenTop !== undefined ? window.screenTop : window.screenY;

    const width = window.innerWidth
      ? window.innerWidth
      : document.documentElement.clientWidth
      ? document.documentElement.clientWidth
      : window.screen.width;
    const height = window.innerHeight
      ? window.innerHeight
      : document.documentElement.clientHeight
      ? document.documentElement.clientHeight
      : window.screen.height;

    const systemZoom = width / window.screen.availWidth;
    const left = (width - w) / 2 / systemZoom + dualScreenLeft;
    const top = (height - h) / 2 / systemZoom + dualScreenTop;
    const newWindow = window.open(
      url,
      title,
      `
      scrollbars=yes,
      width=${w / systemZoom}, 
      height=${h / systemZoom}, 
      top=${top}, 
      left=${left}
      `
    );

    if (window.focus) newWindow.focus();
  };

  componentDidUpdate(prevProps, prevState) {
    if (
      prevProps.user.userData !== this.props.user.userData &&
      this.props.user.userData
    ) {
      this.setState({ userData: this.props.user.userData });

      socket.emit("NewClient", {
        userid: this.props.user.userData._id,
        type: this.props.user.userData.type,
      });
    }
    if (
      this.state.appIsLoading &&
      this.props.profileIsLoading === false &&
      this.props.userIsLoading === false
    ) {
      this.setState({ ...this.state, appIsLoading: false });
    }
    if (
      prevProps.auth.isLogging === true &&
      this.props.auth.isLogging === false &&
      !this.props.auth.error.login_err
    ) {
      if (localStorage.regData) {
        localStorage.removeItem("regData");
      }
      window.location.pathname = "/profile";
    }
  }

  render() {
    const { isAuth, user, profile: prof, loginName } = this.props;
    const { userData = undefined } = user;

    const job_title = prof && prof.job_title;
    const image = prof && prof.image;
    const type = userData && userData.type;

    return (
      <div className="App">
        {this.state.ring && (
          <audio id="ring" autoPlay loop>
            <source src="/audio/incoming-call.mp3" type="audio/mpeg" />
          </audio>
        )}
        {(this.state.appIsLoading && isAuth) ||
        this.props.isLoggingOut === true ? (
          <Loader1 />
        ) : (
          <Router>
            <>
              {isAuth ? (
                <React.Fragment>
                  <div className="popup-call">
                    <div
                      className="modal fade"
                      id="incoming-call"
                      tabIndex="-1"
                      role="dialog"
                      aria-labelledby="exampleModalLabel"
                      aria-hidden="true"
                    >
                      <div className="modal-dialog" role="document">
                        <div className="modal-content">
                          <div className="modal-body text-center">
                            <img
                              className="incoming-call__img"
                              src={this.state.companyImg}
                            ></img>
                            {this.state.companyName} calling you
                          </div>
                          <div className="modal-footer">
                            <button
                              type="button"
                              className="btn btn-danger"
                              onClick={this.reject}
                              data-dismiss="modal"
                            >
                              reject
                            </button>
                            <button
                              type="button"
                              className="btn btn-success"
                              onClick={this.accept}
                              data-dismiss="modal"
                            >
                              response
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  {!window.location.href.includes("interview") && (
                    <NavbarUser
                      name={
                        (userData && userData.name) || loginName || undefined
                      }
                      profession={job_title || "Job"}
                      img={image || "https://placehold.it/50/ddd"}
                      type={type}
                      _logout={(history) => this.props.logout(history)}
                    />
                  )}
                </React.Fragment>
              ) : (
                <NavbarGuest />
              )}
              <div className="main-content">
                <MainPagesRoutes />
              </div>
            </>
          </Router>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state, props) => ({
  isAuth: state.auth.isAuth,
  auth: state.auth,
  isLoggingOut: state.auth.isLoggingOut,
  loginName: state.auth.name,
  user: state.user,
  profile: state.profile.profileData,
  profileIsLoading: state.profile.loaders.profileFetching,
  userIsLoading: state.user.userDataLoading,
});

export default connect(mapStateToProps, {
  logout,
  fetchUserData,
  fetchProfile,
})(App);
