import React, { useState } from "react";

import { useEffect } from "react";

const planToColor = {
  gold: "#B5A900",
  silver: "#949494",
  platinum: "#31B4C9",
  none: "#FF6600",
};
export default ({
  imgSrc = "http://placehold.it/100/ddd",
  companyName = "company name",
  paymentPlan = "none",
}) => {
  const [currentPayPlan, setPayPlan] = useState("none");
  useEffect(() => {
    setPayPlan(paymentPlan);
  }, []);

  return (
    <div
      className="payment-analysis-card main-card-layout px-0"
      style={{
        border: "none",
        boxShadow: "0 0 .2rem rgba(0,0,0,0.3)",
      }}
    >
      <div
        className="responsive_img_container"
        style={{ width: 50, height: 50, borderRadius: "50%" }}
      >
        <img src={imgSrc} alt="company image" style={{ border: "50%" }} />
      </div>
      <p className="my-3">{companyName}</p>
      <div
        className="plan-rectangle w-100 flex-centered py-2"
        style={{ backgroundColor: planToColor[currentPayPlan] }}
      >
        <p style={{ color: "#FFF" }}>current plan {currentPayPlan}</p>
      </div>

      <p style={{ fontWeight: "bold", fontSize: 14 }} className="my-3">
        change plan
      </p>

      <button
        className="input--blank d-flex justify-content-between align-items-center px-5 w-100"
        style={{ background: "transparent" }}
        onClick={() => setPayPlan("gold")}
      >
        <span style={{ color: planToColor["gold"] }}>gold</span>
        <div
          style={{
            width: 15,
            height: 15,
            borderRadius: "50%",
            border: "1px solid #DDD",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {currentPayPlan === "gold" && (
            <i
              className="fas fa-check"
              style={{ fontSize: 10, color: "#333" }}
            />
          )}
        </div>
      </button>
      <button
        className="input--blank d-flex justify-content-between align-items-center px-5 w-100"
        style={{ background: "transparent" }}
        onClick={() => setPayPlan("silver")}
      >
        <span style={{ color: planToColor["silver"] }}>silver</span>
        <div
          style={{
            width: 15,
            height: 15,
            borderRadius: "50%",
            border: "1px solid #DDD",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {currentPayPlan === "silver" && (
            <i
              className="fas fa-check"
              style={{ fontSize: 10, color: "#333" }}
            />
          )}
        </div>
      </button>
      <button
        className="input--blank d-flex justify-content-between align-items-center px-5 w-100"
        style={{ background: "transparent" }}
        onClick={() => setPayPlan("platinum")}
      >
        <span style={{ color: planToColor["platinum"] }}>platinum</span>
        <div
          style={{
            width: 15,
            height: 15,
            borderRadius: "50%",
            border: "1px solid #DDD",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {currentPayPlan === "platinum" && (
            <i
              className="fas fa-check"
              style={{ fontSize: 10, color: "#333" }}
            />
          )}
        </div>
      </button>
      <button
        className="input--blank d-flex justify-content-between align-items-center px-5 w-100"
        style={{ background: "transparent" }}
        onClick={() => setPayPlan("none")}
      >
        <span style={{ color: planToColor["none"] }}>none</span>
        <div
          style={{
            width: 15,
            height: 15,
            borderRadius: "50%",
            border: "1px solid #DDD",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          {currentPayPlan === "none" && (
            <i
              className="fas fa-check"
              style={{ fontSize: 10, color: "#333" }}
            />
          )}
        </div>
      </button>

      <button
        className="btn btn-outline-secondary mt-5"
        style={{ borderRadius: 20 }}
      >
        Show Company Profile
      </button>
    </div>
  );
};
