import {
  FETCH_JOBS_FAILURE,
  FETCH_JOBS_START,
  FETCH_JOBS_SUCCESS
} from "../../types";
import { uris } from "../../../config";
import Axios from "axios";

export const fetchJobs = () => dispatch => {
  dispatch({
    type: FETCH_JOBS_START
  });
  Axios.get(uris.job.getAllJobs)
    .then(({ data }) => {
      dispatch({
        type: FETCH_JOBS_SUCCESS,
        payload: data
      });
    })
    .catch(e => {
      console.error(e);
      dispatch({
        type: FETCH_JOBS_FAILURE
      });
    });
};
