import React from "react";
import AnalysisDataCard from "./AnalysisDataCard";

export default function GeneralAnalysis() {
  return (
    <div className="avoid-navbar general-analysis">
      <section>
        <h6 className="text-secondary title">main analysis</h6>
        <div className="row">
          <AnalysisDataCard
            info="15,458"
            title="companies"
            src="/images/company.svg"
          />
          <AnalysisDataCard
            info="234,123"
            title="applicants"
            src="/images/employee_1.svg"
          />
          <AnalysisDataCard info="78,125" title="jobs" />
        </div>
      </section>
      <section>
        <h6 className="text-secondary title">jobs analysis</h6>
        <div className="row">
          <AnalysisDataCard info="50,545" title="accepted" />
          <AnalysisDataCard info="143,878" title="rejected" />
          <AnalysisDataCard info="11,200" title="on contact" />
        </div>
        <div className="row">
          <AnalysisDataCard info="13,450" title="shortlisted" />
        </div>
      </section>

      <section>
        <h6 className="text-secondary title">Blog</h6>
        <div className="row">
          <AnalysisDataCard info="2000" title="posts" />
        </div>
      </section>

      <section>
        <h6 className="text-secondary title">Events</h6>
        <div className="row">
          <AnalysisDataCard info="125" title="active" />
          <AnalysisDataCard info="100" title="closed" />
        </div>
      </section>
    </div>
  );
}
